body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
header {
  text-align: center;
}
main {
  margin: auto;
  width: 70%;
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}
input[type='text'] {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}
input[type='text'].error {
  color: red;
  border: 1px solid red;
}
input[type='submit'] {
  background-color: #4caf50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
input[type='submit']:hover:not([disabled]) {
  background-color: #45a049;
}
input[type='submit']:disabled {
  background-color: #ddd;
}
